@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
.quiz {
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
}

.question {
    width: 80%;
    background: linear-gradient(to top, rgba(0, 0, 0, 0), #020230);
    background-color: black;
    border: 2px solid white;
    text-align: center;
    padding: 20px;
    border-radius: 50px;
    font-size: 28px;
    text-transform: uppercase;
    font-weight: 600;
}

.answers {
    width: 80%;
    display: flex;
    justify-content: center;
    flex-wrap: wrap-reverse;
}

.answer {
    width: 40%;
    padding: 15px;
    text-align: center;
    background-color: #020230;
    border: 1px solid white;
    border-radius: 50px;
    font-size: 20px;
    font-weight: 400;
    margin: 0 10px 20px 10px;
    cursor: pointer;
    transition: 0.3s ease;
}

.answer.active {
    background: teal;
}

.answer:hover {
    background-color: teal;
    border-radius: 50px;
}
.answer:active {
    background-color: rgb(0, 88, 88);
}

.answer.correct {
    -webkit-animation: correct 5s ease-in forwards;
            animation: correct 5s ease-in forwards;
}

@-webkit-keyframes correct {
    0%,
    22%,
    42%{
        background: #020230;
    }
    20%,
    40%,
    60%{
        background: teal;
    }
    62%,
    100%{
        background: green;
    }
}

@keyframes correct {
    0%,
    22%,
    42%{
        background: #020230;
    }
    20%,
    40%,
    60%{
        background: teal;
    }
    62%,
    100%{
        background: green;
    }
}

.answer.wrong {
    -webkit-animation: wrong 5s ease forwards;
            animation: wrong 5s ease forwards;
}

@-webkit-keyframes wrong {
    0%,
    22%,
    42%{
        background: #020230;
    }
    20%,
    40%,
    60%{
        background: teal;
    }
    62%,
    100%{
        background: rgb(255, 0, 0);
    }
}

@keyframes wrong {
    0%,
    22%,
    42%{
        background: #020230;
    }
    20%,
    40%,
    60%{
        background: teal;
    }
    62%,
    100%{
        background: rgb(255, 0, 0);
    }
}

/* ========== MEDIQUERIES ========== */

/* SMARTPHONE */

@media screen and (max-width: 760px) {
    .question {
        width: 80%;
        margin-top: 30px;
        font-size: 20px;
        text-transform: none;
        margin-bottom: 25px;
    }
    .answer {
        width: 80%;
        padding: 15px;
        text-align: center;
        background-color: #020230;
        border: 1px solid white;
        border-radius: 50px;
        font-size: 20px;
        font-weight: 400;
        margin: 0 10px 20px 10px;
    }
    .answer::after:hover {
        background-color: none;
    }
}
.start {
    width: 450px;
    height: 100px;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    align-items: center;
    position: relative;
    top: -10%;
    right: 0;
    bottom: 0;
    left: 0;
    margin: auto;
}

.startInput::-webkit-input-placeholder {
    font-size: 22px;
    text-transform: none;
}

.startInput:-ms-input-placeholder {
    font-size: 22px;
    text-transform: none;
}

.startInput::placeholder {
    font-size: 22px;
    text-transform: none;
}

.startInput {
    width: 80%;
    height: 40px;
    border: none;
    border-radius: 0px;
    text-align: center;
    font-size: 25px;
    margin-bottom: 20px;
    transition: 0.3s ease;
    text-transform: uppercase;
}

.startInput:focus {
    outline: none;
}

.startButton {
    width: 50%;
    height: 40px;
    font-size: 20px;
    border: none;
    cursor: pointer;
    border-radius: 50px;
    padding: 10px;
    font-weight: 900;
    transition: 0.3s ease;
}

.startButton:hover {
    width: 60%;
}

.startRules {
    padding-top: 50px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.startRulesTitle {
    padding-bottom: 10px;
    font-size: 30px;
}

.startRulesElement {
    padding-top: 20px;
    font-size: 18px;
    font-style: italic;
}

.fa.fa-check {
    color: green;
    padding-right: 10px;
}

/* ========== MEDIQUERIES ========== */

/* SMARTPHONE */

@media screen and (max-width: 760px) {
    .start {
        margin-top: 40%;
    }
}
* {
    margin: 0;
    font-family: 'Montserrat', sans-serif;}

.app {
    height: 100vh;
    display: flex;
    background-color: #020230;
    color: white;
}

.main {
    display: flex;
    flex-direction: column;
    width: 75%;
    background: linear-gradient(to bottom, rgba(0, 0, 0, 0), #020230), url(/who-wants-to-be-a-millionaire/static/media/bg.7bc614ee.jpg) bottom no-repeat;
}

.top {
    height: 30%;
    position: relative;
}

.bottom {
    height: 50%; 
}

.timer {
    position: absolute;
    width: 70px;
    height: 70px;
    border-radius: 50%;
    border: 5px solid white;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 30px;
    font-weight: 700;
    top: 10%;
    left: 10%;
}

.usernameContainer {
    position: absolute;
    width: 50%;
    height: 70px;
    display: flex;
    justify-content: right;
    align-items: center;
    font-size: 30px;
    font-weight: 700;
    top: 10%;
    right: 10%; 
}

.pyramid {
    width: 25%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.moneyList {
    list-style: none;
    width: 100%;
    padding: 20px;
}

.moneyListItem {
    display: flex;
    justify-content: space-around;
    /*justify-content: center; -- MOBILE*/
    align-items: center;
    padding: 10px;
    border-radius: 5px;
}

.moneyListItem.active {
    background-color: teal;
    font-weight: 900;
}

.moneyListItemNumber {
    font-weight: 600;
    font-size: 30px;
}

.moneyListItem.active span.moneyListItemNumber {
    font-weight: 900;
    font-size: 40px;
}

.moneyListItemAmount {
    font-weight: 400;
    font-size: 22px;
}

.moneyListItem.active span.moneyListItemAmount {
    font-weight: 900;
    font-size: 28px;
}

.gameOver {
    position: relative;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    margin: auto;
}

.gameOverText {
    font-size: 60px;
    margin-bottom: 3rem;
    text-shadow: 2px 2px black;
    text-align: center;
}

.gameOverTryAgain {
    width: 40%;
    display: block;
    margin: auto;
    text-align: center;
    font-size: 20px;
    font-weight: 600;
    background-color: white;
    color: black;
    text-decoration: none;
    text-transform: uppercase;
    padding: 10px 20px 10px 20px;
    transition: 0.3s ease;
    border-radius: 50px;
}

.gameOverTryAgain:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

/* ========== MEDIQUERIES ========== */

/* SMARTPHONE */

@media screen and (max-width: 760px) {
    .app {
        height: 100vh;
    }
    .main {
        display: flex;
        flex-direction: column;
        width: 75%;
        background: linear-gradient(to bottom, rgba(0, 0, 0, 0), #020230), url(/who-wants-to-be-a-millionaire/static/media/bg-phone.7bc614ee.jpg) center no-repeat;
    }    
    .top {
        height: 20%;
        position: relative;
    }    
    .bottom {
        height: 50%; 
    }
    .pyramid {
        height: 80vh;
    }
    .usernameContainer {
        position: absolute;
        text-transform: uppercase;
        width: 30%;
        height: 70px;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 15px;
        font-weight: 700;
        top: 10%;
        right: 10%;            
    }
    .moneyListItemNumber {
        display: none;
    }
    .gameOver {
        margin-top: 25%;
    }
    .gameOverTryAgain {
        width: 70%;
    }
}
